body {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: Roboto, sans-serif;
  font-size: 12px;
  font-weight: 500;
  text-decoration: none;
}

:root {
  --black: #000;
  --blue: #00acdc;
}

.header {
  justify-content: space-between;
  align-items: center;
  padding: 0 50px;
  display: flex;
  box-shadow: 0 2px 4px #0000;
}

a {
  text-decoration: none;
}

.nav-list {
  margin: 0;
  padding: 0;
  list-style-type: none;
  display: flex;
}

.nav-item:not(:last-child) {
  margin-right: 20px;
}

.nav-link {
  text-transform: uppercase;
  color: var(--black);
  padding: 23px 0;
  font-family: Roboto, sans-serif;
  font-size: 12px;
  font-weight: 500;
  line-height: 14px;
  display: block;
}

.nav-link:hover, .is-active {
  color: var(--blue);
}

.is-active {
  position: relative;
}

.is-active:after {
  content: "";
  background-color: var(--blue);
  border-radius: 8px;
  width: 100%;
  height: 4px;
  position: absolute;
  bottom: 0;
  left: 0;
}
